:root {
  --font: "oswaldmedium", "Arial", sans-serif;
  --font-second: "Arial", sans-serif;
  --white: #ffffff;
  --black: #000000;
  --green: #68b738;
  --bg-green: #68b738;
  --grey: #444444;
  --grey-2: #e6e6e6;
  --special-grey: #666666;
  --special-grey-2: #d9d9d9;
  --bg-grey: #f2f2f2;
  --border-color: #cdcdcd;
  --border-color-2: #dcdcdc;
  --bg-grey-2: #eaeaea;
  --black-2: #111111;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
@font-face {
  font-family: "oswaldmedium";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "oswaldmedium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Blogger Bold"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff2") format("woff");
}
.header {
  padding-top: 10px;
  padding-bottom: 13px;
}
.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .nav-menu {
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .nav-menu {
    padding-left: 7.7%;
    padding-right: 7.7%;
  }
}
.nav-menu__img {
  width: 34px;
  height: 38px;
}
@media (min-width: 768px) {
  .nav-menu__img {
    width: 174px;
    height: 52px;
    margin-top: 10px;
    margin-left: 11px;
  }
}
@media (min-width: 1440px) {
  .nav-menu__img {
    margin: 0;
    margin-top: 41px;
    width: 202px;
    height: 59px;
  }
}
.nav-menu__img-part2 {
  width: 101px;
  height: 18px;
  margin-right: 10px;
}
@media (min-width: 768px) {
  .nav-menu__img-part2 {
    display: none;
  }
}
.nav-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: none;
}
@media (min-width: 768px) {
  .nav-menu__list {
    display: flex;
    flex-direction: row;
    margin-left: 50px;
    margin-top: 10px;
    padding-right: 12px;
  }
}
@media (min-width: 1440px) {
  .nav-menu__list {
    margin: 0;
    padding-right: 0;
    margin-top: 3%;
  }
}
.nav-menu__item {
  padding-top: 23px;
  padding-bottom: 20px;
  text-align: center;
  position: relative;
}
.nav-menu__item::before {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: var(--grey-2);
  top: -1px;
}
@media (min-width: 768px) {
  .nav-menu__item::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav-menu__item {
    margin-left: 35px;
  }
}
.nav-menu__link {
  color: var(--black);
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
}
.nav-menu__link:hover {
  opacity: 0.6;
}
.nav-menu__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav-menu__link {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .nav-menu__link {
    color: var(--white);
    font-size: 20px;
    line-height: 30px;
  }
}
.nav-menu__button {
  position: relative;
  width: 30px;
  height: 20px;
  font-size: 0;
  background-color: transparent;
  border: 0;
}
.nav-menu__button::before {
  position: absolute;
  right: 0;
  top: 0;
  content: "";
  width: 26px;
  height: 2px;
  display: block;
  background-color: var(--black);
  box-shadow: 0 8px 0 var(--black), 0 16px 0 var(--black);
}
@media (min-width: 768px) {
  .nav-menu__button {
    display: none;
  }
}
.nav-menu--opened {
  position: relative;
}
.nav-menu--opened .nav-menu__list {
  display: flex;
  top: 55px;
  flex-direction: column;
  position: absolute;
  background-color: var(--white);
  width: 100%;
  padding-left: 0;
  padding-right: 0;
  left: -50%;
  transform: translateX(50%);
  z-index: 3;
}
.nav-menu--opened .nav-menu__button {
  position: relative;
}
.nav-menu--opened .nav-menu__button::before,
.nav-menu--opened .nav-menu__button::after {
  background-color: var(--black);
  content: "";
  display: block;
  position: absolute;
  box-shadow: none;
  transform: rotate(45deg);
  right: -1px;
  top: 9px;
  width: 26px;
  height: 2px;
}
.nav-menu--opened .nav-menu__button::after {
  transform: rotate(-45deg);
}
.nav-menu__link--active {
  position: relative;
}
@media (min-width: 768px) {
  .nav-menu__link--active::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: var(--bg-green);
    bottom: -8px;
  }
}
.main-intro {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
