    display: block;
    left: 50%;
    width: 720px;
    height: 694px;
    top: -35.4%;
  }
}
.main-intro__background::after {
  width: 100%;
  content: "";
  background-color: var(--green);
  display: block;
  height: 100%;
  opacity: 0.85;
}
.main-intro__background--change {
  background-image: url("../img/index-background-mobile@1x.png");
