.location__address {
  margin-left: 16px;
  color: var(--grey);
  font-family: var(--font-second);
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .location__address {
    text-align: right;
    font-size: 16px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .location__address {
    margin-top: 20px;
    margin-left: 0;
    text-align: left;
  }
}
.location__map {
  position: relative;
  left: 0;
  right: 0;
  width: calc(100vw - (100vw - 100%));
  /*width: 100vw;*/
  /*margin-left: calc(50% - 50vw - 0px);*/
  height: 362px;
  background-color: var(--white);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .location__map {
    height: 400px;
  }
}
.map__google {
  position: absolute;
  left: 0;
  /*right: 0;*/
  height: 100%;
  /* Рекомендация Google*/
  /*width: calc(100vw - (100vw - 100%));*/
  z-index: 10;
  overflow: hidden;
  width: 100vw;
  margin-left: calc(50% - 50vw - 9px);
  /*width: calc(100vw - (100vw - 100%));*/
}
.icon-facebook,
.icon-vk,
.icon-instagram {
  fill: var(--special-grey);
}
.icon-vk__path,
.icon-instagram__path {
  fill: var(--special-grey);
}
.icon-facebook__path {
  fill: transparent;
  stroke: var(--special-grey);
}
.icon-vk:hover .icon-vk path {
  fill: var(--bg-green);
  stroke: var(--bg-green);
  fill: #eda;
  stroke: #1fa;
}
